import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-day1',
  templateUrl: './day1.component.html',
  styleUrls: ['./day1.component.css']
})
export class Day1Component implements OnInit {

  ergebnis: number = 0;
  captcha: string;
  half: number = 0;

  constructor() {
  }

  ngOnInit() {
  }

  antwortberechnen(): void {
    this.captcha = "";
    console.log(this.captcha);
    this.half = this.captcha.length / 2;

    for (let i = 0; i <= this.captcha.length - 1; i++) {
      if (this.captcha[i] == this.captcha[(i + this.half) % this.captcha.length]) {
        this.ergebnis = this.ergebnis + Number.parseInt(this.captcha[i]);
      }
    }
  }

}



